import * as PIXI from "pixi.js"
// @ts-ignore
import block from "./block3.png"

export class Block {
  private sprite: PIXI.Sprite
  private speed: number = 0

  constructor(pixi: PIXI.Application) {
    this.sprite = PIXI.Sprite.from(block)
    this.sprite.pivot.x = 30
    this.sprite.pivot.y = 30
    pixi.stage.addChild(this.sprite)
    this.resetPosition()
  }

  public update(delta:number) {
    this.sprite.rotation += 0.01
    this.sprite.x -= this.speed
    if (this.sprite.x < -100) this.resetPosition()
  }

  private resetPosition() {
    this.speed = 1 + Math.random()
    this.sprite.x = 800
    this.sprite.y = Math.random() * 570
  }
}
